import React, { Component } from 'react';
import SweetAlert from 'react-sweetalert2';
class UDSweetAlert extends React.Component {

  // state is for keeping control state before or after changes.
  state = {

    // the things you want to put in state.
    // text: this.props.text //un comment the line to use state insted props
  }

constructor(){
        super();
        this.state = {
          swal: {},
          open: true
        }
  }
  render() {
       return <SweetAlert show={this.state.open} type={this.props.types} title={this.props.title} text={this.props.text} footer={this.props.footer} showloading={this.props.showloading} position={this.props.position} timer={this.props.timer} showConfirmButton={true} showCancelButton={this.props.showCancelButton} confirmButtonText={this.props.confirmButtonText} cancelButtonText={this.props.cancelButtonText} showCloseButton={this.props.showCloseButton} onClose={()=>{this.setState({open: false})}} />
          }
    }

export default UDSweetAlert
